<template>
  <div class="operate-task-log">
    <div class="topTitle">
      <span class="topTitle-span1">客户管理</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">个人客户</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">缺项管理</span>
      <div class="button-content">
        <Button type="primary" @click="exportFn">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="客户号：" >
                <Input v-model="formValidate.val1" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="客户名称：" >
                <Input v-model="formValidate.val2" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="所属业务条线：" >
                <Select v-model="formValidate.val3" style="width:200px">
                  <Option value="">请选择</Option>
                  <Option :value="item.code" v-for="(item,index) in CD0031":key='item.code'>{{item.name}}</Option>
                  <!-- <Option value="beijing">New York</Option>
                  <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option> -->
                </Select>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('0')">查询</Button>
                <Button type="primary" @click="handleSubmit('1')">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>{{item.csnm}}</td>
                  <!-- 客户号 -->
                  <td>{{item.ctnm}}</td>
                  <!-- 客户名称 -->
                  <td>
                     <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.citp"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.citp}}</p>
                  </el-popover>
                  </td>
                  <!-- 证件类型 -->
                  <td>{{item.ctid}}</td>
                  <!-- 证件号码 -->
                  <td>{{item.civt}}</td>
                  <!-- 证件有效期 -->
                  <td>
                    <!-- {{item.ctvc}} -->
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.ctvc"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.ctvc}}</p>
                    </el-popover>
                    </td>
                  <!-- 职业 -->
                  <td>
                     <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.country"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.country}}</p>
                  </el-popover>
                  </td>
                  <!-- 国籍 -->
                  <td>{{item.sex}}</td>
                  <!-- 性别 -->
                  <td>{{item.education}}</td>
                  <!-- 学历 -->
                  <td>{{item.address}}</td>
                  <!-- 联系地址 -->
                  <td>{{item.tel}}</td>
                  <!-- 手机 -->
                  <td>{{item.mbrc}}</td>
                  <!-- 所属业务条线 -->
                  <td>{{item.createSystem}}</td>
                  <!-- 创建渠道 -->
                  <td>{{item.updateSystem}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page  :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"></Page>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
created(){
  
  let that=this;
      this.axios
      .get("/system/getDic/CD0031")
      .then(response => {
        if (response.code == "200") {
          that.CD0031=response.data.CD0031
        }
      })
      .catch(error => {});
      this.tableList();
},
  data() {
    return {
      searchFlag:'0',
      CD0031:[],
      pageSize: 10,
      total: 0,
      pageNum: 1,
      tableHeader: [
        { name: "序" },
        { name: "客户号" },
        { name: "客户名称" },
        { name: "证件类型" },
        { name: "证件号码" },
        { name: "证件有效期" },
        { name: "职业" },
        { name: "国籍" },
        { name: "性别" },
        { name: "学历" },
        { name: "联系地址" },
        { name: "手机（联系方式）" },
        { name: "所属业务条线" },
        { name: "创建系统（渠道）名称" },
        { name: "更新系统（渠道）名称" },
      ],

      tableBody: [

      ],
      formValidate: {
        val1: "",
        val2: "",
        val3: "",
      },
      ruleValidate: {
        val1: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val2: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          }
        ],
        val3: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val4: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
        download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
      let that = this;
      let data = {
        flag:this.searchFlag,
        csnm:this.formValidate.val1,
        ctnm:this.formValidate.val2,
        mbrc:this.formValidate.val3,
      };
      data = JSON.stringify(data);
      this.axios
        .post("/client/stanPerEmptyWarn/doExportPerEmptyWarnInfo", data, {
         headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
    handleSubmit(a){
      this.searchFlag=a;
      this.pageNum=1;
      this.tableList();
    },
    pageChangeFun(a){
      this.pageNum=a*1;
      this.tableList();
    },
    pageSizeChange(a){
      this.pageSize=a*1;
      this.tableList();
    },
    tableList() {
      let that = this;
      let data = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        flag:this.searchFlag,
        csnm:this.formValidate.val1,
        ctnm:this.formValidate.val2,
        mbrc:this.formValidate.val3,
      };
      data = JSON.stringify(data);
      this.axios
        .post("/client/stanPerEmptyWarn/getPerEmptyWarnInfo", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
           this.tableBody=response.data.records;
          this.total = response.data.total;
          }
        })
        .catch(error => {});
    },
  }
};
</script>
<style scoped>
.tableBox tr > td:nth-child(2) {
  min-width: 100px;
}
.tableBox tr > td:nth-child(3) {
  min-width: 160px;
}
</style>



